.filter-dropdown {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-width: 200px;
}

.filter-label {
  font-size: 0.9rem;
  font-weight: 500;
  color: #333;
  margin-bottom: 0.2rem;
}

.select-container {
  position: relative;
  display: inline-block;
}

.filter-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  background-color: white;
  font-size: 1rem;
  color: #333;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.filter-select:focus {
  outline: none;
  border-color: #4CAF50;
  box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.1);
}

.filter-select:hover {
  border-color: #ccc;
}

.select-arrow {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
  pointer-events: none;
  font-size: 0.8rem;
  transition: transform 0.2s ease;
}

.filter-select:focus + .select-arrow {
  transform: translateY(-50%) rotate(180deg);
}

/* Responsive design */
@media (max-width: 768px) {
  .filter-dropdown {
    min-width: 180px;
  }
  
  .filter-select {
    padding: 0.6rem 2rem 0.6rem 0.8rem;
    font-size: 0.95rem;
  }
  
  .filter-label {
    font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .filter-dropdown {
    min-width: 100%;
  }
  
  .filter-select {
    padding: 0.5rem 1.8rem 0.5rem 0.7rem;
    font-size: 0.9rem;
  }
  
  .select-arrow {
    right: 0.8rem;
    font-size: 0.7rem;
  }
}