.plant-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  position: relative;
  border: 1px solid #e0e0e0;
}

.plant-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.plant-card.out-of-stock {
  opacity: 0.7;
  position: relative;
}

.plant-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  gap: 1rem;
}

.plant-name {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
  margin: 0;
  line-height: 1.3;
  flex: 1;
}

.stock-status {
  font-size: 0.8rem;
  font-weight: 500;
  padding: 0.3rem 0.6rem;
  border-radius: 20px;
  white-space: nowrap;
}

.stock-status.in-stock {
  background-color: #e8f5e8;
  color: #2e7d32;
}

.stock-status.out-of-stock {
  background-color: #ffebee;
  color: #c62828;
}

.plant-price {
  font-size: 1.5rem;
  font-weight: 700;
  color: #4CAF50;
  margin-bottom: 1rem;
}

.plant-categories {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.category-tag {
  background-color: #f5f5f5;
  color: #666;
  padding: 0.3rem 0.8rem;
  border-radius: 16px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid #e0e0e0;
}

.out-of-stock-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(198, 40, 40, 0.9);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-weight: 600;
  font-size: 0.9rem;
  pointer-events: none;
}

/* Responsive design */
@media (max-width: 768px) {
  .plant-card {
    padding: 1.2rem;
  }
  
  .plant-card-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .plant-name {
    font-size: 1.1rem;
  }
  
  .plant-price {
    font-size: 1.3rem;
  }
  
  .stock-status {
    align-self: flex-end;
  }
}

@media (max-width: 480px) {
  .plant-card {
    padding: 1rem;
  }
  
  .plant-name {
    font-size: 1rem;
  }
  
  .plant-price {
    font-size: 1.2rem;
  }
  
  .category-tag {
    font-size: 0.75rem;
    padding: 0.25rem 0.6rem;
  }
}