.add-plant-form-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem 0;
}

.form-header {
  text-align: center;
  margin-bottom: 2rem;
}

.form-title {
  font-size: 2.2rem;
  font-weight: 700;
  color: #333;
  margin: 0 0 0.5rem 0;
}

.form-subtitle {
  font-size: 1rem;
  color: #666;
  margin: 0;
}

.success-message {
  background-color: #e8f5e8;
  border: 1px solid #4CAF50;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 2rem;
}

.success-content {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.success-icon {
  font-size: 1.2rem;
  flex-shrink: 0;
}

.success-content p {
  flex: 1;
  margin: 0;
  color: #2e7d32;
  font-weight: 500;
}

.close-success {
  background: none;
  border: none;
  color: #2e7d32;
  cursor: pointer;
  padding: 0.2rem;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  transition: background-color 0.2s ease;
}

.close-success:hover {
  background-color: rgba(46, 125, 50, 0.1);
}

.add-plant-form {
  background-color: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  font-size: 0.95rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.5rem;
}

.form-input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e0e0e0;
  border-radius: 6px;
  font-size: 1rem;
  color: #333;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  box-sizing: border-box;
}

.form-input:focus {
  outline: none;
  border-color: #4CAF50;
  box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.1);
}

.form-input.error {
  border-color: #f44336;
}

.form-input.error:focus {
  border-color: #f44336;
  box-shadow: 0 0 0 3px rgba(244, 67, 54, 0.1);
}

.form-input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
  opacity: 0.7;
}

.form-help {
  display: block;
  font-size: 0.8rem;
  color: #666;
  margin-top: 0.3rem;
  line-height: 1.4;
}

.error-text {
  display: block;
  font-size: 0.85rem;
  color: #f44336;
  margin-top: 0.3rem;
  font-weight: 500;
}

.checkbox-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.3rem;
}

.form-checkbox {
  width: auto;
  margin: 0;
  cursor: pointer;
}

.checkbox-label {
  font-size: 0.95rem;
  font-weight: 500;
  color: #333;
  cursor: pointer;
  margin: 0;
}

.form-actions {
  margin-top: 2rem;
  text-align: center;
}

.submit-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 0.9rem 2rem;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.1s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 140px;
  justify-content: center;
}

.submit-button:hover:not(:disabled) {
  background-color: #45a049;
}

.submit-button:active:not(:disabled) {
  transform: translateY(1px);
}

.submit-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  transform: none;
}

/* Responsive design */
@media (max-width: 768px) {
  .add-plant-form-container {
    padding: 1.5rem 0;
  }
  
  .form-title {
    font-size: 1.9rem;
  }
  
  .add-plant-form {
    padding: 1.5rem;
  }
  
  .form-input {
    padding: 0.65rem;
    font-size: 0.95rem;
  }
  
  .submit-button {
    padding: 0.8rem 1.5rem;
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .add-plant-form-container {
    padding: 1rem 0;
  }
  
  .form-title {
    font-size: 1.7rem;
  }
  
  .form-subtitle {
    font-size: 0.9rem;
  }
  
  .add-plant-form {
    padding: 1.2rem;
  }
  
  .form-group {
    margin-bottom: 1.2rem;
  }
  
  .form-input {
    padding: 0.6rem;
    font-size: 0.9rem;
  }
  
  .submit-button {
    width: 100%;
    padding: 0.75rem;
    font-size: 0.9rem;
  }
  
  .success-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .close-success {
    align-self: flex-end;
  }
}