.loader-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  min-height: 200px;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader-spinner {
  border: 3px solid #f3f3f3;
  border-top: 3px solid #4CAF50;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

/* Size variants */
.loader.small .loader-spinner {
  width: 20px;
  height: 20px;
  border-width: 2px;
}

.loader.medium .loader-spinner {
  width: 40px;
  height: 40px;
  border-width: 3px;
}

.loader.large .loader-spinner {
  width: 60px;
  height: 60px;
  border-width: 4px;
}

.loader-message {
  margin-top: 1rem;
  color: #666;
  font-size: 0.9rem;
  text-align: center;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive design */
@media (max-width: 480px) {
  .loader-container {
    padding: 1.5rem;
    min-height: 150px;
  }
  
  .loader.medium .loader-spinner {
    width: 30px;
    height: 30px;
  }
  
  .loader-message {
    font-size: 0.8rem;
  }
}