.plant-catalog {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 0;
}

.catalog-header {
  text-align: center;
  margin-bottom: 2rem;
}

.catalog-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #333;
  margin: 0 0 0.5rem 0;
}

.catalog-subtitle {
  font-size: 1.1rem;
  color: #666;
  margin: 0;
}

.catalog-filters {
  display: flex;
  gap: 2rem;
  align-items: flex-end;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.catalog-content {
  min-height: 400px;
}

.catalog-results-info {
  margin-bottom: 1.5rem;
  padding: 0 0.5rem;
}

.results-count {
  font-size: 1rem;
  color: #666;
  margin: 0;
}

.filter-info {
  font-weight: 500;
  color: #4CAF50;
}

.no-results {
  text-align: center;
  padding: 3rem 2rem;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.no-results-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.no-results h3 {
  font-size: 1.5rem;
  color: #333;
  margin: 0 0 0.5rem 0;
}

.no-results p {
  color: #666;
  margin: 0 0 1.5rem 0;
}

.clear-filters-btn {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: background-color 0.2s ease;
}

.clear-filters-btn:hover {
  background-color: #45a049;
}

.plants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  padding: 0.5rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .plant-catalog {
    padding: 1.5rem 0;
  }
  
  .catalog-title {
    font-size: 2rem;
  }
  
  .catalog-subtitle {
    font-size: 1rem;
  }
  
  .catalog-filters {
    flex-direction: column;
    gap: 1.5rem;
    align-items: stretch;
    padding: 1.2rem;
  }
  
  .plants-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1.2rem;
    padding: 0.3rem;
  }
  
  .no-results {
    padding: 2rem 1.5rem;
  }
  
  .no-results-icon {
    font-size: 3rem;
  }
}

@media (max-width: 480px) {
  .plant-catalog {
    padding: 1rem 0;
  }
  
  .catalog-title {
    font-size: 1.8rem;
  }
  
  .catalog-filters {
    gap: 1rem;
    padding: 1rem;
  }
  
  .plants-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 0.2rem;
  }
  
  .no-results {
    padding: 1.5rem 1rem;
  }
  
  .no-results h3 {
    font-size: 1.3rem;
  }
  
  .clear-filters-btn {
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
  }
}