.search-bar {
  width: 100%;
  max-width: 500px;
}

.search-input-container {
  position: relative;
  display: flex;
  align-items: center;
  background-color: white;
  border: 2px solid #e0e0e0;
  border-radius: 25px;
  padding: 0.5rem 1rem;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.search-input-container:focus-within {
  border-color: #4CAF50;
  box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.1);
}

.search-icon {
  color: #666;
  margin-right: 0.5rem;
  font-size: 1rem;
}

.search-input {
  flex: 1;
  border: none;
  outline: none;
  font-size: 1rem;
  color: #333;
  background: transparent;
}

.search-input::placeholder {
  color: #999;
}

.clear-button {
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  padding: 0.2rem;
  margin-left: 0.5rem;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.clear-button:hover {
  background-color: #f0f0f0;
  color: #666;
}

.clear-button:active {
  transform: scale(0.95);
}

/* Responsive design */
@media (max-width: 768px) {
  .search-bar {
    max-width: 100%;
  }
  
  .search-input-container {
    padding: 0.6rem 1rem;
  }
  
  .search-input {
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .search-input-container {
    padding: 0.5rem 0.8rem;
  }
  
  .search-input {
    font-size: 0.9rem;
  }
  
  .search-icon {
    font-size: 0.9rem;
    margin-right: 0.4rem;
  }
  
  .clear-button {
    width: 20px;
    height: 20px;
    font-size: 0.8rem;
  }
}